import React, {useState, useEffect} from 'react';
import {observer} from "mobx-react";
import User from "./Stores/User";
import loginStyles from "./style/loginStyle";
import {Typography, Box, TextField, Button, Paper} from '@material-ui/core';
import {Alert} from '@material-ui/lab';

const Auth = () => {
    const classes = loginStyles();

    const [stateLogin, setLogin] = useState(User.token);
    const [statePassword, setPassword] = useState(User.password);

    useEffect(() => {
        User.addJWTToken(localStorage.getItem('JWTToken'));
        User.userBytoken();
    }, []);

    const inputChangeHandlerLogin = (event) => {
        let login = event.target.value;
        setLogin(login);
    };
    const inputChangeHandlerPassword = (event) => {
        let password = event.target.value;
        setPassword(password);
    };


    let login = (event) => {
        event.preventDefault();
        let data = {
            login: stateLogin,
            password: statePassword
        }

        User.Auth(data);
    }

    let logout = (event) => {
        User.logout();
    }


    return (
        !User.checkAuth ?
            (<>
                <Paper className={classes.paper}>
                    <Typography component="h1" variant="h4" align="center" className={classes.textHeader}>
                        
                    </Typography>
                    <Box className={classes.containerForm}>
                        <Alert severity="error" className={!User.message ? classes.disabled : ''}>{User.message}</Alert>
                        <form className={classes.form} noValidate>
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                onChange={inputChangeHandlerLogin}
                                id="login"
                                label=""
                                value={stateLogin}
                                name="login"
                                autoComplete="login"
                                autoFocus
                            />
                            <TextField
                                margin="normal"
                                onChange={inputChangeHandlerPassword}
                                required
                                fullWidth
                                value={statePassword}
                                name="password"
                                label=""
                                type="password"
                                id="password"
                                autoComplete="current-password"
                            />
                            <Button
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                onClick={login}
                                className={classes.submit}
                            >
                                
                            </Button>
                        </form>
                    </Box>
                </Paper>
            </>)
            :
            (<Paper className={classes.paper}>
                <p>id: {User.user.userId}</p>
                <Box className={classes.containerForm}>
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        onClick={logout}
                        className={classes.submit}
                    >
                        
                    </Button>
                </Box>
            </Paper>)
    );
};

export default observer(Auth)
